<template>
  <div class="login">
    <div class="login__input-wrap">
        <span class="p-float-label login__username">
        <label for="username" style="margin-bottom: 10px;">{{ info.username }}</label>
        <InputText id="username" type="text" v-model="username"/>
        </span>
    </div>
    <div class="login__input-wrap">
      <span class="p-float-label login__username">
        <label for="username" style="margin-bottom: 10px;">{{ info.password }}</label>
      <Password v-model="password" toggleMask class="p-input-icon-right"></Password>
      </span>
    </div>
    <Button @click="$emit('finish')">{{ info.btn }}</Button>
    <slot></slot>
  </div>
</template>

<script>
import InputText from 'primevue/inputtext';
import Password from 'primevue/password';
import Button from 'primevue/button';

export default {
  name: "AuthForm",
  components: {Button, InputText, Password},
  props: ["username", "password", "info"],
  computed: {
    username: {
      get() {
        return this.username
      },
      set(value) {
        this.$emit("update:username", value);
      },
    },
    password: {
      get() {
        return this.password
      },
      set(value) {
        this.$emit("update:password", value);
      },
    }
  }
}
</script>

<style lang="scss">
.sizes {
  .p-inputtext {
    display: block;
    margin-bottom: .5rem;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.p-password {
  display: flex;
  align-items: center;
}

.p-input-icon-right > i:last-of-type {
  position: absolute;
  top: 50%;
  margin-top: -0.5rem;
}

.field * {
  display: block;
}

.login {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;

  &__username {
    display: flex;
    flex-direction: column;
  }

  &__input-wrap {
    margin-bottom: 20px;
  }
}
</style>
